.item {
    display: flex;
    justify-content: space-between;
    max-width: 300px;
    padding: 15px 0 15px 15px;
}

.myContainer{
    max-width: 605px;
    height: 36px;
    display: flex;
    align-items: center;
    background-color: rgba(229, 229, 229, 1);

}

.itemWrapper{
    border-bottom: 1px solid rgba(229, 229, 229, 1);
    max-width: 605px;
}

.itemWrapper:last-of-type{
    border-bottom: none;
}


.item:last-of-type {

}

