}

.buttonsContainer {
    max-width: 605px;
    justify-content: flex-end;
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
}

.container {
}

.nameCol {
    font-weight: 600;
    font-size: 14px;
    line-height: 16px;
    font-family: Roboto, sans-serif;
}

.nameUserCol{
    font-weight: 300;
